/* fonts from Google */
@import url('https://fonts.googleapis.com/css?family=Hind');

.Grid-cell {
  display: inline-block;
  width: calc(90%/3);
}

button {
  border-radius: 50%;
  width: 70px;
  height: 70px;
  border: 1px solid #7151A4;
  background-color: #553D7B;
  padding: 20px;
  margin: 10px;
  font-size: 20px;
  font-family: 'Hind', sans-serif;
  color: white;
  text-align: center;
  -webkit-transition: background-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out;
}


button:focus {
  outline: 0;
}

button:active {
  background-color: #7151A4;
}

.delete-button {
  border-radius: 50%;
  width: 70px;
  height: 70px;
  border: 1px solid #F34235;
  background-color: #F34235;
  padding: 20px;
  margin: 10px;
  font-size: 20px;
  font-family: 'Hind', sans-serif;
  color: white;
  text-align: center;
  -webkit-transition: background-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out;
}

.delete-button:active {
  background-color: rgba(243, 66, 53, 0.3);
}

.validate-button {
  border-radius: 50%;
  width: 70px;
  height: 70px;
  border: 1px solid #00C853;
  background-color: #00C853;
  padding: 20px;
  margin: 10px;
  font-size: 20px;
  font-family: 'Hind', sans-serif;
  color: white;
  text-align: center;
  -webkit-transition: background-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out;
}

.validate-button:active {
  background-color: rgba(0, 200, 83, 0.3);
}



